import Vue from "vue";
import VueRouter from "vue-router";
import Store from "../views/store.vue";
import Cart from "../views/cart.vue";
import Home from "../views/home.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/home",
    name: "Home",
    component: Home
  },
  {
    path: "/",
    name: "Store",
    component: Store
  },
  {
    path: "/cart",
    name: "Cart",
    component: Cart
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "active-route", // active class for non-exact links.
  linkExactActiveClass: "active-route-exact" // active class for *exact* links.
});

export default router;
