<script>
import axios from "axios";

export default {
  name: "App",

  components: {},

  data: () => ({
    activeBtn: 1,
    tab: null,
    products: null,
    vegetables: null,
    fruits: null,
    seafood: null,
    showcnt: false,
    num: 1,
    pitems: [
      {
        icon: "folder",
        qty: "120g",
        title: "Green Chilli",
        subtitle: "₨. 72.00"
      },
      {
        icon: "folder",
        qty: "300g",
        title: "Recipes",
        subtitle: "Jan 17, 2014"
      },
      { icon: "folder", qty: "500g", title: "Work", subtitle: "Jan 28, 2014" }
    ],
    items: ["Vegetables", "Fruits", "Sea Food Collection", "Organic Rice"],
    text:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat."
  }),
  created() {
    axios
      .get(
        `https://script.google.com/macros/s/AKfycbxgX8WS7051I8afxFpj9-TQEELKIvsqFckrzfRSa3jGKZ4-EAY4/exec`
      )
      .then(response => {
        // JSON responses are automatically parsed.
        this.products = response.data;
        this.fruits = response.data.Products.filter(product =>
          product.Categories.includes("Fruits")
        );
        this.vegetables = response.data.Products.filter(product =>
          product.Categories.includes("Vegetables")
        );
        this.seafood = response.data.Products.filter(product =>
          product.Categories.includes("Sea Food Collection")
        );
        console.log(this.fprod);
      })
      .catch(e => {
        this.errors.push(e);
      });

    // async / await version (created() becomes async created())
    //
    // try {
    //   const response = await axios.get(`http://jsonplaceholder.typicode.com/posts`)
    //   this.posts = response.data
    // } catch (e) {
    //   this.errors.push(e)
    // }
  }
};
</script>

